.single {
  display: flex;
  width: 100%;
}
.singleContainer {
  flex: 6;
}
.topSingle {
  display: flex;
  padding: 20px;
  gap: 20px;
}
.leftSingle {
  flex: 1;
  -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
  box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
  padding: 20px;
  position: relative;
}

.editButton {
  position: absolute;
  top: 0;
  right: 0;
  padding: 5px;
  font-size: 12px;
  color: #7451f8;
  background-color: #7551f818;
  cursor: pointer;
  border-radius: 0 0 0 5px;
}

.titleSingle {
  margin: 0 0 20px 0;
  font-size: 16px;
  color: lightgray;
}
.itemSingle {
  display: flex;
  gap: 20px;
}
.itemImg {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  object-fit: cover;
}
.detailTitle {
  margin: 0 0 10px 0;
  color: #555;
}
.detailItem {
  margin-bottom: 10px;
  font-size: 14px;
}
.itemKey {
  font-weight: bold;
  color: grey;
  margin-right: 5px;
}
.itemValue {
  font-weight: 300;
}

.rightSingle {
  flex: 2;
}

.bottomSingle {
  -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
  box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
  padding: 20px;
  margin: 10px 20px;
}
