.widget {
  display: flex;
  flex: 1;
  justify-content: space-between;
  padding: 10px;
  -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
  box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
  border-radius: 10px;
  height: 100px;
}

.left,
.right {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.title1 {
  font-weight: bold;
  font-size: 14px;
  color: rgb(160 160, 160);
  margin: 0;
}

.counter1 {
  font-weight: 300px;
  font-size: 28px;
  /* color: black; */
  margin: 0;
}

.link {
  font-size: 12px;
  border-bottom: 1px solid gray;
  width: max-content;
  margin: 0;
}

.percentage {
  display: flex;
  font-size: 14px;
  align-items: center;
}
.positive {
  color: green;
}
.negative {
  color: red;
}

.iconWidget {
  font-size: 18px;
  padding: 5px;
  border-radius: 5px;
  align-self: flex-end;
}
